@import url(https://fonts.googleapis.com/css2?family=Nunito:ital,wght@0,300;0,400;0,600;0,700;0,800;1,700;1,800&display=swap);
html {
  box-sizing: border-box;
  scroll-behavior: smooth;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  margin: 0;
  font-family: 'Nunito', sans-serif;
}

.App{display:flex;flex-direction:column;justify-content:center;align-items:center}

.View{display:flex;width:100%;height:120vh}

.Decorate{background-color:#D7D7D7;width:50vw;display:flex;flex-direction:column;justify-content:center;align-items:center}.Decorate__container{display:block;flex-direction:column;justify-content:center;align-items:center}.Decorate__image{max-width:600px}

.Header{text-align:center;font-size:36px;font-weight:700;margin:48px 0 0 0}.Header__w{background:linear-gradient(180deg, #FFE321 0%, #F5C001 85.94%);-webkit-background-clip:text;-webkit-text-fill-color:transparent;-webkit-box-decoration-break:clone;box-decoration-break:clone}

.Content{display:flex;flex-direction:column;justify-content:center;align-items:center;background-color:#171820;color:white;width:50vw}.Content__header{margin:0 0 48px 0;font-size:48px;font-weight:700;text-align:center;max-width:600px}.Content__paragraph{text-align:center;margin:0 0 80px 0;font-size:20px;font-weight:500;line-height:42px;max-width:600px}.Content__span{color:#FFE321;background:linear-gradient(180deg, #FFE321 0%, #F5C001 85.94%);-webkit-background-clip:text;-webkit-text-fill-color:transparent;-webkit-box-decoration-break:clone;box-decoration-break:clone}

.Slider{font-weight:800;font-style:italic;color:white;text-align:center;font-size:72px}.Slider__yellow{color:#FFE321;background:linear-gradient(180deg, #FFE321 0%, #F5C001 85.94%);-webkit-background-clip:text;-webkit-text-fill-color:transparent;-webkit-box-decoration-break:clone;box-decoration-break:clone}

.Button{width:224px;height:56px;border-radius:30px;font-size:30px;font-weight:700;font-family:'Nunito', sans-serif;background:linear-gradient(180deg, #FFE321 0%, #F5C001 85.94%);box-shadow:0 0 10px rgba(0,0,0,0.25);border:none}.Button:focus{outline:1px solid #171820}

.Video{background-color:#D7D7D7;width:50vw;display:flex;flex-direction:column;justify-content:center;align-items:center;overflow:hidden}.Video__source{width:auto;height:100%}

.Contact{display:flex;justify-content:space-around;align-items:flex-start;padding-top:176px;width:100%;height:calc(74vh - 76px);background-color:#171820}.Contact__container{text-align:center}.Contact__header{color:white;font-size:48px;margin:0 0 72px 0}.Contact__link{display:block;font-size:48px;font-weight:700;text-decoration:none;margin:0 0 48px 0;color:#FFE321;background:linear-gradient(180deg, #FFE321 0%, #F5C001 85.94%);-webkit-background-clip:text;-webkit-text-fill-color:transparent;-webkit-box-decoration-break:clone;box-decoration-break:clone}

.Form{color:white;display:flex;flex-direction:column;align-items:center}.Form__header{font-size:48px;font-weight:700;margin:0 0 32px 0;text-align:center}.Form__container{display:flex;margin-bottom:16px}.Form__box{background-color:#C4C4C4;border:2px solid #FFE321;width:240px;height:50px;border-radius:16px;display:flex;align-items:center}.Form__box--topic{margin-right:24px}.Form__label{font-size:12px;font-weight:600;padding-left:20px;padding-right:4px;color:rgba(23,24,32,0.5)}.Form__input{background-color:#C4C4C4;border:none;width:160px}.Form__input:focus{outline:1px solid #171820}.Form__textarea{width:500px;height:132px;box-sizing:border-box;background-color:#C4C4C4;border-radius:16px;border:2px solid #FFE321;padding:8px;margin-bottom:16px}.Form__textarea::-webkit-input-placeholder{font-size:12px;font-weight:600;color:rgba(23,24,32,0.5);font-family:"Nunito", sans-serif}.Form__textarea:-ms-input-placeholder{font-size:12px;font-weight:600;color:rgba(23,24,32,0.5);font-family:"Nunito", sans-serif}.Form__textarea::-ms-input-placeholder{font-size:12px;font-weight:600;color:rgba(23,24,32,0.5);font-family:"Nunito", sans-serif}.Form__textarea::placeholder{font-size:12px;font-weight:600;color:rgba(23,24,32,0.5);font-family:"Nunito", sans-serif}.Form__button-box{align-self:flex-start}

.Banner{background-color:#D7D7D7;display:flex;justify-content:space-around;align-items:center;height:26vh;width:100%}.Banner__paragraph{text-align:center;font-size:18px;font-weight:600;max-width:220px}.Banner__image{height:160px;width:auto}

.Navigation{width:100%;height:76px;position:fixed;top:0;left:0;z-index:10}.Navigation__nav{width:100%;height:76px;display:flex;align-items:center}.Navigation__left{height:100%;width:50%;background-color:#D7D7D7;display:flex;justify-content:flex-start;align-items:center}.Navigation__right{height:100%;width:50%;background-color:#171820;display:flex;justify-content:flex-end;align-items:center}.Navigation__home{margin-left:32px}.Navigation__link{font-size:20px;font-weight:800;color:#D7D7D7;text-decoration:none;margin-right:48px}

