@import 'src/variables';

.Button {
  width: 224px;
  height: 56px;
  border-radius: 30px;

  font-size: 30px;
  font-weight: 700;
  font-family: 'Nunito', sans-serif;

  background: $yellowGradient;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.25);
  border: none;

  &:focus {
    outline: 1px solid $darkBackground;
  }
}
